$primary:#AA0601;
$primary-dark:#650300;
$bg-secondary:#FBF2F2;
$black-900:#000;
$black-500:#707070;
$white:#fff;
$shadow-color:rgba(0, 0, 0, 0.16);
$spacer:1rem;
$link-hover:rgba(227, 227, 227, 0.89);
$button-hover:rgba(143, 4, 0, 1);
$pagination-color:#555555;
$blog-text:#555555;



body{
    font-family: 'Noto Sans TC', 'Roboto' , -apple-system, BlinkMacSystemFont,  "PingFang TC", "Segoe UI", "Microsoft JhengHei", Roboto, "Helvetica Neue", Arial, sans-serif;
    line-height: 1.5;
    font-weight: 400;
    color:$black-900;
}

.container{
    max-width: 1368px;
    margin: 0 auto;
    padding: 0  36px;

}
@media (max-width:767px){
    .container{
        padding-left: 13px;
        padding-right: 14px;
    }
}
.container__contact{
    max-width: 708px;

}

h2{
    font-size: 64px;
    font-weight: 700;
   
}
h3{
    font-size: 48px;
    font-weight: 700;
   
}

h4{
    font-size: 42px;
    font-weight: 500;
    
}
h5{
    font-size: 32px;
}
h6{
    font-size: 24px;
}
.text-48-bold{
    font-size: 48px;
    font-weight: 700;
}
.font-italic{
    font-style: italic;
}
.d-flex{
    display: flex;
}
.jcsb{
    justify-content: space-between;
}
.jcc{
    justify-content: center;
}
.align-center{
align-items: center;
}
.text-center{
    text-align: center;
}
.family-roboto-500{
    font-family: 'Roboto';
    font-weight: 500;
}
.family-roboto{
    font-family: 'Roboto';
}
.weight-700{
    font-weight: 700;
}
.mb-1{
    margin-bottom: $spacer*.25;
}
.mb-2{
    margin-bottom: $spacer*0.5;
}
.mb-3{
    margin-bottom: $spacer;
}
.mb-4{
    margin-bottom: $spacer*1.5;
}
.mb-5{
    margin-bottom: $spacer*2;
}
.mb-6{
    margin-bottom: $spacer*3;
}
.mb-7{
    margin-bottom: $spacer*3.5;
}

.btn-primary{
    cursor: pointer;
    color:$white;
    background-color: $primary;
    border: none;
    padding: 0;
}

.input-primary{
    border: 1px solid black;
    padding: 6px 12px;
}
::placeholder{
    color:rgba(0, 0, 0, 0.20);
}

.content-title{
    font-size: 48px;
    font-weight: 700;
    padding-left: 16px;
    position: relative;
    margin-bottom: 32px;
    &::after{
        content: "";
        height: 72px;
        width: 8px;
        position: absolute;
        top: 0;
        left:-4px;
        background: $primary;
    }
}
@media (max-width:767px){
    .content-title{
        font-size: 24px;
        margin-bottom: 24px;
        &::after{
            height: 36px;
        }
    }
}

.blog__nav{
    background-color: $primary-dark;

    .blog__nav__content{
        gap:0px 48px;
        a{
            padding: 13px 0px;
            font-size: 20px;
            color:$white;
            font-weight: 700;
            transition: all .3s;
            &:hover{
                color:$link-hover;
            }
        }
    }
}
@media (max-width:767px) {
    .blog__nav{
        display: none;
    }
}
.blog__nav--inner{
    background-color: $primary-dark;

    .blog__nav__content{
        gap:0px 48px;
        a{
            padding: 13px 0px;
            font-size: 20px;
            color:$white;
            font-weight: 700;
            transition: all .3s;
            &:hover{
                color:$link-hover;
            }
        }
    }
}
@media (max-width:767px) {
    .blog__nav--inner{
        .blog__nav__wrapper{
            display: inherit;
        }
        .blog__nav__content{
            justify-content: space-between;
            gap:0px 24px;
            text-align: center;
            li{
                width: 100%;
            }
        }
    .icon__search{
        display: none;
    }
    }
}